import { Box, TextField, Typography } from "@mui/material";
import { height } from "@mui/system";
import React, { useState } from "react";

const colorList = ["", "", "", "", ""] as const;
const animalList = ["", "", "", "", "", "", "", "", "", "", "", ""] as const;

type AnimalColor = typeof colorList[number];
type AnimalType = typeof animalList[number];
type AnimalMap = Record<AnimalType, string>;
type HEXColor = `#${string}`;

const ColorToHEX: Record<AnimalColor, HEXColor> = {
    "": "#00A80F",
    "": "#FF0000",
    "": "#FFD700",
    "": "#FFFFFF",
    "": "#000000",
}

const AnimalToImg: AnimalMap = {
    "": "https://upload.wikimedia.org/wikipedia/commons/1/15/WildRat.jpg",
    "": "https://upload.wikimedia.org/wikipedia/commons/thumb/0/0c/Cow_female_black_white.jpg/939px-Cow_female_black_white.jpg",
    "": "https://upload.wikimedia.org/wikipedia/commons/thumb/b/b9/P.t.altaica_Tomak_Male.jpg/997px-P.t.altaica_Tomak_Male.jpg",
    "": "https://upload.wikimedia.org/wikipedia/commons/6/6d/Arctic_hare.jpg",
    "": "https://upload.wikimedia.org/wikipedia/commons/thumb/d/df/Smaug_par_David_Demaret.jpg/1118px-Smaug_par_David_Demaret.jpg",
    "": "https://upload.wikimedia.org/wikipedia/commons/5/5a/Atheris_hispida.jpg",
    "": "https://upload.wikimedia.org/wikipedia/commons/f/f8/Pernod_Al_Ariba_0046b.jpg",
    "": "https://upload.wikimedia.org/wikipedia/commons/e/e7/Ovis_orientalis_aries_%27Skudde%27_%28aka%29.jpg",
    "": "https://upload.wikimedia.org/wikipedia/commons/thumb/8/8b/Macaca-fascicularis_flk18447014.jpg/1083px-Macaca-fascicularis_flk18447014.jpg",
    "": "https://upload.wikimedia.org/wikipedia/commons/f/f0/Kura_domowa_DSC08721.JPG",
    "": "https://upload.wikimedia.org/wikipedia/commons/c/c0/Fm_shiba_inu_puppy.jpg",
    "": "https://upload.wikimedia.org/wikipedia/commons/4/48/Vietnamese_Pot_Bellied_Pig.jpg"
}

type AnimalWithColorProps = {
    color: AnimalColor;
    animal: AnimalType;
}

const AnimalWithColor = ({ animal, color }: AnimalWithColorProps) => (
    <div style={{ border: "2px solid black" }}>
        <img style={{ height: 300, width: "100%" }} src={AnimalToImg[animal]} alt="" />
        <div style={{ backgroundColor: ColorToHEX[color], height: 50, width: "100%" }}></div>
    </div >
)


const Calendar = () => {
    const [year, setYear] = useState(1984);

    const colorIndex = Math.floor((year % 10) / 2);
    const animalIndex = year % 12;

    const handleYearChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        setYear(Number(e.target.value));
    }

    return (
        <div>
            <AnimalWithColor animal={animalList[animalIndex]} color={colorList[colorIndex]} />
            <Box display="flex" alignItems="center" justifyContent="center" sx={{ margin: "15px" }}>
                <TextField label="" inputProps={{ min: "0" }} value={year} type="number" onChange={handleYearChange}></TextField>
            </Box>
        </div>

    );
}

export default Calendar;